<template>
    <div>
        <headers :title="title"></headers>
        <div class="add-write">
            <ul>
                <li>
                    <font class="fl">收件人姓名</font><input class="fl col-1" placeholder="请输入姓名">
                </li>
                 <li>
                    <font class="fl">联系方式</font><input class="fl col-1" placeholder="请输入联系方式">
                </li>
                 <li @click="status">
                    <font class="fl">所在地区</font><span class="fl col-1" style="color: rgb(129,131,137);">{{address}}</span><i class="fr"></i>
                </li>
                 <li>
                    <font class="fl">详细地址</font><input class="fl col-1" placeholder="请输入详细地址">
                </li>
            </ul>
        </div>
        <footers :footerData="footerData"></footers>
        <van-area v-if="show" :areaList="areaList" @cancel="status" @confirm="confirm"/>
    </div>
</template>

<style lang="less" scoped>
  .van-area {
    left: 0;
  }

  .add-write {
    font-family: "PingFang-SC-Medium";
    margin-top: 115px;
    background: #fff;
    border-top: 1px solid #D4D4D6;
    width: 750px;
    ul {
      padding-left: 32px;
      li {
        font-size: 28px;
        color: #000;
        height: 98px;
        border-bottom: 1px solid #D4D4D6;
        font {
          font-family: "PingFang-SC-Medium";
          color: #000000;
          width: 212px;
          line-height: 98px;
        }
        span.col-0 {
          color: #000;
          line-height: 98px
        }
        .col-1 {
          line-height: 98px
        }
        .col-1::-webkit-input-placeholder {
          color: rgb(129, 131, 137);
        }
        i {
          width: 16px;
          height: 98px;
          background: url("../assets/imgs/add-go.png") no-repeat left center;
          background-size: 100%;
          margin-right: 33px;
        }
      }
    }
  }

  .van-area {
    position: absolute;
    bottom: 0;
    width: 100%;
  }
</style>

<script>
  import "lib-flexible"
  import headers from '../components/editHeader.vue'
  import footers from '../components/editfooter.vue'
  import jarea from '../mock/area.json'
  export default {
    components: {
      headers,
      footers
    },
    data() {
      return {
        title: "地址填写",
        footerData: "保存",
        areaList: jarea,
        show: false,
        address: '请选择地区'
      }
    },
    methods: {
      status() {
        this.show = !this.show
      },
      confirm(data) {
        this.address = data[0].name + data[1].name + data[2].name;
        this.show = !this.show;
      }
    },
    mounted() {
      document.body.style.backgroundColor = "rgb(247,246,247)";
    }
  }
</script>



